import React from "react";

//import FormInput from "../form-input/form-input.component";
import SubmitButton from "../submit-button/submit-button.component";

class TVZoneQuestion extends React.Component {
  continue = event => {
    event.preventDefault();
    this.props.nextQuestion();
  };
  render() {
    return (
      <div className='add-room'>
        <h2>TV Zone</h2>
        <span>Does this room have a TV?</span>

        <form className='add-room' onSubmit={this.continue}>
          <input
            type='text'
            name='roomName'
            handleChange={this.props.handleChange}
            label='Room Name'
            required
            autoFocus
          />
          <div className='buttons'>
            <SubmitButton type='submit'>Next</SubmitButton>
            <SubmitButton
              type='submit'
              onClick={this.handleSubmitStayInPage}
              inverted
            >
              Keep adding Rooms
            </SubmitButton>
          </div>
        </form>
      </div>
    );
  }
}

export default TVZoneQuestion;
